.intro{
    display: flex;
    height: 77vh;
    margin-top: 6rem;
    
}
.i-left{
    display: flex;
    position: relative;
    flex:1;
    flex-direction: column;
    border: 1px solid;
    gap:2rem
}
.i-name{
    display: flex;
    flex-direction: column;

}

.i-name>:nth-child(1){
  color: var(--black);
  font-weight: bold;
  font-size: 3rem;
    
}

.i-name>:nth-child(2){
    color: var(--orange);
    font-weight: bold;
    font-size: 3rem;
      
  }

  .i-name>:nth-child(3){
    color: var(--gray);
    font-size: 14px;
    margin-top: 10px;
      /* font-family: var(--newfont); */
  }
.i-right{
    border: 1px solid;
    width:40%
}
.i-button{
width:6rem;
height: 2rem;
}