.n-wrapper{
    height:20px;
    display:flex;
    justify-content: space-between;
    /* border: 1px solid; */
    /* font-family: var(--font-family); */
    margin-top: 10px;
}
.n-left{
    flex:1;
   align-items: center;
   display: flex;
   gap:2rem
}

.n-name{
    font-size: 1rem;
    font-weight: bold;
    /* font-family: var(--font-family); */
}

.n-right{
    display: flex;
    flex:1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
    
}

n-list{
    flex:10
}

.n-list>ul{
    display: flex;
    gap:2rem;
    margin-right: 4rem;
    
}
li{
    /* font-family: var(--font-family); */
}
.n-list>ul>li:hover{
  cursor: pointer;
  color:var(--teal)
}

