:root {
  --teal: rgb(66, 158, 158);
  --orange: rgb(11, 199, 199);
  --black: rgb(35, 35, 35);
  --gray: rgb(89, 88, 85);
  --orange: #FCA61F;
  --blueCard: rgb(254, 254, 254);
  --white: white;
  --boxShadow: rgb(160, 126, 63);
  /* --font-family: sans-serif; */
}

.App {
  padding: 0.5rem 3.5rem;
  overflow: hidden;
  color: var(--black)
}

.button {
  border-radius: 34px;
  border: none;
  color: white;
  font-size: 16px;
  padding: 10px;
  margin-bottom: 6px;
  background-color: teal;
  /* font-family: sans-serif; */
}

.button:hover {
  background-color: var(--white);
  cursor: pointer;
  color: teal;
  border: 2px solid teal;
  rotate: 360;
  padding: -px;
}

* {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  --newfont: "sans-serif"
}